html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

b,
strong {
  font-weight: 700;
}

i {
  font-style: italic;
}

@font-face {
  font-family: "Suisse Intl";
  src: url("../fonts/suisse-intl/SuisseIntl-Regular.woff2") format("woff2"), url("../fonts/suisse-intl/SuisseIntl-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Suisse Intl";
  src: url("../fonts/suisse-intl/SuisseIntl-Medium.woff2") format("woff2"), url("../fonts/suisse-intl/SuisseIntl-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Manrope";
  src: url("../fonts/manrope/Manrope-Regular.woff2") format("woff2"), url("../fonts/manrope/Manrope-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Manrope";
  src: url("../fonts/manrope/Manrope-Bold.woff2") format("woff2"), url("../fonts/manrope/Manrope-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Manrope";
  src: url("../fonts/manrope/Manrope-ExtraBold.woff2") format("woff2"), url("../fonts/manrope/Manrope-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

input,
button,
textarea,
select {
  font-size: inherit;
  font-family: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
  outline: none;
  font-size: inherit;
  background: transparent;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover {
  text-decoration: none;
}

img,
picture {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

a {
  color: inherit;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
  font-family: "Suisse Intl", sans-serif;
  font-size: 16px;
  line-height: 1.02em;
  background-color: #F0F7F0;
}

body.no-scroll {
  overflow: hidden;
}

.main {
  flex-grow: 1;
}

html,
body {
  height: 100%;
}

.m-container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1820px;
  margin: 0 auto;
  padding: 0 28px;
}

*:focus {
  outline: none;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

.site-main {
  width: 100%;
  flex-grow: 1;
}

.page {
  overflow: hidden;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

[data-fancybox] {
  cursor: pointer;
}

.text-editor {
  color: rgba(1, 50, 32, 0.4);
}

.text-editor--light {
  color: rgba(255, 255, 255, 0.45);
}

.text-editor > *:first-child {
  margin-top: 0 !important;
}

.text-editor > *:last-child {
  margin-bottom: 0 !important;
}

.text-editor .info-tag {
  margin-bottom: 25px;
}

.text-editor .text-items-list {
  margin-bottom: 20px;
}

.text-editor .text-items-list > *:first-child {
  margin-top: 0;
}

.text-editor .text-items-list__note {
  margin-top: 18px;
  max-width: 430px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.02em;
  color: #04412B;
  opacity: 0.3;
}

.text-editor__columns {
  display: flex;
  gap: 80px;
  align-items: flex-start;
}

.text-editor__column {
  flex: 0 1 50%;
}

.text-editor__column > *:first-child {
  margin-top: 0 !important;
}

.text-editor__column > *:last-child {
  margin-bottom: 0 !important;
}

.text-editor h2,
.text-editor h3 {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.02em;
  color: #015738;
}

.text-editor b {
  color: #04412B;
  font-weight: inherit;
}

.text-editor--light b {
  color: #fff;
}

.text-editor p,
.text-editor a {
  display: block;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.55em;
}

.text-editor p a {
  margin: 0;
  display: inline;
  text-decoration: none;
}

.text-editor a:hover,
.text-editor a:hover > * {
  color: #4C9E50;
}

.text-editor a,
.text-editor a > * {
  transition: color 0.3s;
}

.text-editor ul {
  padding-left: 28px;
  list-style: none;
  margin-bottom: 20px;
}

.text-editor ul > li {
  position: relative;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.55em;
}

.text-editor ul > li::before {
  content: "";
  position: absolute;
  top: 0.775em;
  left: -22px;
  transform: translateY(-50%);
  width: 5px;
  height: 5px;
  background-color: rgba(1, 50, 32, 0.4);
  border-radius: 100%;
}

.text-editor ul > li:has(b):before {
  background-color: #04412B;
}

.text-editor ol {
  list-style: none;
  counter-reset: num;
  color: #04412B;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.55em;
}

.text-editor ol li {
  position: relative;
  padding: 15px 20px 15px 60px;
}

.text-editor ol li:nth-child(odd) {
  background-color: #F0F7F0;
}

.text-editor ol li::before {
  content: counter(num) "";
  counter-increment: num;
  position: absolute;
  top: 15px;
  left: 20px;
  font-weight: 500;
}

.section {
  padding-left: 100px;
  overflow: hidden;
}

.gost-section {
  padding: 20px 28px;
}

.gost-section--bordered {
  border-top: 1px solid #CCCCCC;
}

.gost-section__header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
}

.gost-section__header .gost-section__text {
  max-width: 380px;
}

.gost-section__title {
  max-width: 590px;
}

.gost-section__title + h2,
.gost-section__title + h3,
.gost-section__title + p,
.gost-section__title + a {
  margin-top: 60px;
}

.gost-section__text {
  max-width: 750px;
}

.gost-section__text a {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.02em;
  text-decoration: underline;
  color: #4C9E50;
}

.e-program-header {
  margin-bottom: 80px;
  padding-top: 30px;
  display: flex;
  align-items: flex-start;
  gap: 40px;
}

.e-program-header__item {
  flex: 0 1 50%;
}

.e-program-header__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.02em;
  color: #015738;
}

.e-program-header__text {
  margin-top: 12px;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.55em;
  color: #04412B;
}

.title {
  font-size: 72px;
  color: #04412B;
}

.title--light {
  color: #fff;
}

.title span {
  color: #4C9E50;
}

.title h1,
.title h2,
.title h3,
.title h4,
.title h5,
.title h6 {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
  letter-spacing: inherit;
  font-weight: inherit;
  text-wrap: inherit;
}

.title__h1 {
  font-size: 1em;
  font-weight: 500;
  line-height: 0.91em;
  letter-spacing: -0.025em;
}

.title__h2 {
  font-size: 0.8333em;
  font-weight: 700;
  line-height: 1.2em;
}

.title__h3 {
  font-size: 0.6875em;
  font-weight: 700;
  line-height: 1.2em;
}

.title__h4 {
  font-size: 0.5833em;
  font-weight: 700;
  line-height: 1.2em;
}

.title__h5 {
  font-size: 0.4792em;
  font-weight: 700;
  line-height: 1.2em;
}

.title__h6 {
  font-size: 0.3958em;
  font-weight: 700;
  line-height: 1.2em;
}

.breadcrumbs {
  position: relative;
  z-index: 3;
  overflow: hidden;
}

.breadcrumbs__list {
  list-style-type: none;
  display: flex;
  gap: 9px;
  font-size: 16px;
  line-height: 1.02em;
  font-weight: 400;
  color: #04412B;
  padding-bottom: 6px;
  overflow-x: auto;
  overflow-y: hidden;
}

.breadcrumbs__list::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}

.breadcrumbs__list::-webkit-scrollbar-track {
  background-color: transparent;
}

.breadcrumbs__list::-webkit-scrollbar-thumb {
  background-color: #015738;
  border-radius: 8px;
}

.breadcrumbs__item {
  display: flex;
  align-items: center;
  gap: 9px;
  white-space: nowrap;
}

.breadcrumbs__item a {
  transition: color 0.3s;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.breadcrumbs__item a:hover {
  color: #4C9E50;
}

.breadcrumbs__item--current {
  color: #AFB2B8;
  pointer-events: none;
}

.breadcrumbs__separator {
  width: 7px;
  height: 7px;
  border-radius: 100%;
  background-color: #4C9E50;
}

.breadcrumbs--light .breadcrumbs__list {
  color: white;
}

.breadcrumbs--light .breadcrumbs__back .btn__text {
  color: white;
}

.breadcrumbs--light .breadcrumbs__back .btn__icon {
  color: white;
}

.tabs-block {
  width: 100%;
  overflow: hidden;
}

.tabs-block__tabs {
  display: flex;
  align-items: stretch;
  padding: 0 29px 5px;
  overflow-x: auto;
}

.tabs-block__tabs::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.tabs-block__tabs::-webkit-scrollbar-track {
  background-color: transparent;
}

.tabs-block__tabs::-webkit-scrollbar-thumb {
  background-color: #015738;
  border-radius: 8px;
}

.tabs-block__tab {
  width: 469px;
  min-height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 30px;
  cursor: pointer;
  font-family: "Manrope", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.366em;
  color: #04412B;
  background-color: #E6F0EC;
  transition: color 0.3s, background-color 0.3s;
}

.tabs-block__tab--active {
  color: #fff;
  background-color: #04412B;
}

.tabs-block__slides {
  margin-top: 35px;
  border-top: 1px solid #CCCCCC;
}

.tabs-block__slide {
  display: none;
}

.tabs-block__slide--active {
  display: block;
}

.tabs-block--mini .tabs-block__tab {
  font-size: 14px;
  width: auto;
  line-height: 1em;
  min-height: 1em;
}

.link-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 34px;
  color: #04412B;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.02em;
}

.link-block--note {
  background-color: #F0F7F0;
  padding: 22.5px 14px 22.5px 36px;
  border-left: 4px solid #4C9E50;
  margin-top: 10px;
}

.link-block--table {
  padding-bottom: 22px;
  margin-top: 22px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.link-block.link-block--link a.link-block__link {
  flex: 0 0 137px;
}

.link-block__desc {
  flex: 1 1 auto;
  max-width: 775px;
}

.link-block a.link-block__link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: flex-start;
  gap: 14px;
  flex: 0 0 214px;
  min-height: 54px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.02em;
  margin-bottom: 0;
}

.link-block__text {
  display: block;
  white-space: nowrap;
}

.link-block__icon {
  flex: 0 0 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #013220;
  border: 1px solid #04412B;
  border-radius: 100%;
  color: #fff;
}

.link-block__icon svg {
  max-width: 17.52px;
  max-height: 17.59px;
  width: 100%;
  height: 100%;
}

.info-tag {
  width: fit-content;
  padding: 11px 23px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.02em;
  color: #fff;
  background-color: #4C9E50;
  border-radius: 27px;
}

.count-block {
  display: flex;
  justify-content: space-between;
}

.count-block--row {
  align-items: center;
  background-color: #F0F7F0;
  padding: 20px 25px;
  margin-top: 18px;
  gap: 50px;
}

.count-block__desc {
  flex: 1 1 auto;
  color: #04412B;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.17em;
}

.count-block__num {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.02em;
  color: #015738;
}

.count-block--row .count-block__num {
  flex: 0 0 71px;
}

.count-block--column {
  flex-direction: column;
  padding: 20px 0;
  gap: 5px;
}

.socials {
  display: flex;
  align-items: center;
  gap: 10px;
}

.socials__item {
